@import "base";

@import "align";
@import "anchor";
@import "container";
@import "figure";
@import "indented";
@import "nav";

@import "custom/embeded";
@import "custom/icons";
@import "custom/notes";
@import "custom/syntax-highlighting";

@import "section/self";
@import "section/post_footer";
@import "section/utterances";

@import "settings";

pre {
  overflow-x: scroll;
}

.container.post {
  padding-bottom: 4em;
}

.post {
  $transition: width 0.2s ease-in-out;

  transition: $transition;

  &.full {
    width: 100%;
    transition: $transition;
  }

  ol,
  ul {
    // This is originally set by the content on the index page.
    width: unset;
  }
}

.title {
  margin-bottom: 0;
}

.byline {
  color: $faded-color;
  margin: 1rem 0 1rem;

  + * {
    margin-top: 0;
  }
}

@media only screen and (max-width: $mobile-width) {
  .post-group {
    h1,
    h2,
    h3,
    p,
    .summary {
      width: 100%;
    }

    h1 {
      font-size: 2.5rem;
      line-height: 3rem;
    }

    p,
    .summary {
      line-height: 1.8rem;
    }

    h2,
    h3 {
      line-height: 2.5rem;
    }
  }
}
