@import "settings";

span {
  &.newthought {
    font-size: 1.2em;
    font-variant: small-caps;
    letter-spacing: 0.05rem;
  }

  &.tag,
  &.croissant {
    color: $faded-color;
    display: inline-block;
    font-family: $code-font;
    margin-left: 1em;
  }

  &.el {
    color: $faded-color;
  }
}

.emoji {
  height: 1em;
  vertical-align: -5%;
  width: 1em;
}
