@import "settings";

.note * {
  cursor: pointer;
}

.container.post {
  @media only screen and (min-width: $inline-notes-width) {
    transform: translateX(-20%);
  }
}

.sidenote,
.marginnote {
  clear: right;
  float: right;
  margin-right: -45%;
  position: relative;
  text-align: justify;
  word-break: break-word;
  overflow-x: scroll;
  overflow-y: hidden;
  vertical-align: baseline;
  width: 40%;
  z-index: 10;

  @media only screen and (max-width: $inline-notes-width) {
    display: none;
  }

  &.maybe-hide {
    // Only hide on full-width toggle, which does not occur on mobile.
    @media only screen and (min-width: $inline-notes-width) {
      display: none;
    }
  }

  &::-webkit-scrollbar {
    -webkit-appearance: none;
    height: 7px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: rgba(0, 0, 0, 0.4);
    border-radius: 7px;
  }

  &::-webkit-scrollbar-corner {
    display: none;
  }
}

.sidenote-number {
  counter-increment: sidenote-counter;
  display: inline;

  &::after {
    content: counter(sidenote-counter);
    font-size: 0.7rem;
    font-weight: 500;
    left: 0.1rem;
    top: -0.5rem;
  }
}

.note * {
  cursor: pointer;
}

.margin-toggle {
  &:not(.sidenote-number) {
    display: none;
  }
}

.sidenote::before,
.marginnote::before,
.sidenote-number::after {
  font-family: $body-font;
}

.sidenote-number::after,
.sidenote::before {
  color: $contrast-color;
  content: counter(sidenote-counter);
  position: relative;
  vertical-align: baseline;
}

.sidenote::before {
  content: counter(sidenote-counter) ". ";
  top: 0;
}

@media only screen and (max-width: $inline-notes-width) {
  .margin-toggle:checked {
    + .sidenote,
    + .marginnote {
      clear: both;
      display: block;
      float: left;
      left: 1rem;
      margin: 1rem 2.5%;
      position: relative;
      vertical-align: baseline;
      width: 95%;
    }
  }

  label {
    cursor: pointer;

    &.margin-toggle:not(.sidenote-number) {
      color: $contrast-color;
      display: inline;
    }
  }
}
