@import "settings";

section.nav {
  margin-bottom: 2em;
}

.nav-link {
  color: rgba($black, 0.6);
  font-size: 1.2em;

  &:not(.no-margin) {
    margin-right: 2em;
  }

  &:not(*) {
    color: $link-color;
  }
}

.bottom-bar {
  padding: 0 1em;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  font-size: 0.7em;

  details {
    &[open] > summary {
      font-weight: bold;
    }
    > summary {
      color: rgba($black, 0.6);
      cursor: help;
      list-style-type: none;
      outline: none;
    }
    > summary::-webkit-details-marker {
      display: none;
    }
  }
}
