@import "settings";

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-left: -1em;
  padding-left: 1em;
  position: relative;

  &:hover .header-anchor {
    display: block;

    &:before {
      left: 0;
      top: 0;
      content: "#";
    }
  }
}

a {
  &.header-anchor {
    top: 0;
    color: $faded-link-color;
    display: none;
    font-style: normal;
    left: 0.2em;
    position: absolute;

    &:hover,
    &:active {
      color: $link-color;
      display: block;
    }
  }
}
