@import "settings";

section.self {
  @media only screen and (max-width: 960px) {
    align-items: flex-end;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
  }
}

section.self-links {
  .link-container:last-of-type {
    margin-bottom: 0;
  }

  .link-title {
    display: block;
    line-height: 1em;
  }
}

