@import "settings";

html {
  font-size: 12px;

  @media screen and (min-width: 350px) {
    font-size: 13px;
  }
  @media screen and (min-width: 600px) {
    font-size: 14px;
  }
  @media screen and (min-width: 900px) {
    font-size: 15px;
  }
  @media screen and (min-width: 1000px) {
    font-size: 16px;
  }

  height: 100%;
}

body,
html {
  margin: 0;
  width: 100%;
}

body {
  -webkit-text-size-adjust: 100%;
  -ms-hyphens: auto;
  -webkit-hyphens: auto;
  hyphens: auto;
  background-color: $bg-color;
  color: $text-color;
  counter-reset: sidenote-counter;
  font-family: $body-font;
  font-weight: 300;
  line-height: 2em;
  margin: auto;
  min-height: 100%;
  text-align: justify;
  overflow-x: hidden;
  position: relative;
}

h1,
h2,
h3 {
  font-weight: 400;
  line-height: 1;
  margin: 1.5em 0;
  text-align: left;
}

h2,
h3 {
  margin-bottom: 0;
}

h1 {
  font-size: 3.2rem;
  margin-bottom: 1.5rem;
  margin-top: 0;
}

h2 {
  font-size: 2.4rem;
}

h3 {
  font-size: 1.7rem;
}

a {
  font-weight: 400;
  text-decoration: none;

  &,
  &:hover,
  &:active {
    color: $link-color;
  }

  &:hover {
    text-decoration: underline;
  }
}

code {
  font-family: $code-font;
}
