@import "settings";

.gist {
  margin-left: -2em;

  @media only screen and (max-width: $mobile-width) {
    margin-left: 0;
    width: 100%;
  }

  table {
    &.highlight {
      margin: 0.5em 0;
    }
  }

  tbody tr td {
    &.blob-num,
    &.blob-code {
      font-size: 11px;

      @media screen and (min-width: 350px) {
        font-size: 12px;
      }
      @media screen and (min-width: 600px) {
        font-size: 13px;
      }
      @media screen and (min-width: 900px) {
        font-size: 14px;
      }
    }
  }

  .gist-meta {
    display: none;
  }

  .gist-data {
    border-bottom: none !important;
  }
}

.mathblock {
  overflow-x: scroll;
  @media only screen and (max-width: $mobile-width) {
    width: 100%;
  }
}

.resp-iframe {
  border: 0;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

.resp-iframe-container {
  overflow: hidden;
  padding-top: 56.25%; // 9/16 ratio
  position: relative;
}
