@import "settings";

blockquote {
  opacity: 0.8;
}

blockquote,
ol,
ul {
  -webkit-padding-start: 5%;
  margin-left: 0;
  margin-right: 0;
  padding-left: 2em;

  p,
  li,
  ol,
  ul {
    width: 100%;
  }
}

ol,
ul {
  // width: calc(#{$post-content-width} + 2em);

  @media screen and (max-width: $mobile-width) {
    width: unset;
  }
}
