img {
  max-width: 100%;
}

figure {
  -webkit-margin-end: 0;
  -webkit-margin-start: 0;
  border: 0;
  font: inherit;
  font-size: 100%;
  padding: 0;
  vertical-align: baseline;
  text-align: center;
}

figcaption {
  font-size: 0.8rem;
  line-height: 1.5;
  margin-bottom: 0;
  margin-top: 0;
  padding-left: 10%;
  position: relative;
  text-align: center;
  vertical-align: baseline;
  width: 80%;

  code {
    font-size: 0.8rem;
  }
}

.math {
  outline-offset: 0.5em;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  &.premise {
    outline: 1px dashed black;
  }
  &.problem {
    outline: 1px solid black;
  }
  &.prop,
  &.lemma,
  &.cor {
    position: relative;
    &::after {
      font-size: 1.8em;
      content: "\25FB";
      position: absolute;
      bottom: 0;
      right: 0;
    }
  }
  &.algorithm {
    overflow: scroll;
    blockquote {
      opacity: 1;
    }
    li {
      font-family: $code-font;
    }
  }
}
