@mixin vert-frame-padding {
  padding: 9em 3em;

  @media only screen and (max-width: $mobile-width) {
    padding: 2em 7.5%;
  }
}

.container {
  @include vert-frame-padding;

  display: flex;
  justify-content: center;
  margin: auto;
  max-width: 800px;
  position: relative;
}

.panel {
  display: flex;
  flex-flow: row wrap-reverse;
  width: 100%;

  @media only screen and (max-width: 960px) {
    flex-flow: column-reverse;
  }
}

.column-left {
  display: flex;
  flex: 1 0;
  justify-content: center;
  margin: 5em 2em 0 0;
  max-width: 100%;

  @media only screen and (max-width: $mobile-width) {
    margin: 2em 0;
  }
}

.column-right {
  flex: 3 0;
  max-width: 100%;
  margin: auto;
}
